import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RdPage } from './rd.page';

const routes: Routes = [
  {
    path: '',
    component: RdPage
  },
  {
    path: 'rdview',
    loadChildren: () => import('./rdview/rdview.module').then( m => m.RdviewPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RdPageRoutingModule {}
